import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { AlertController } from 'ionic-angular';

import { ChatRoomPage } from '../chatroom/chatroom';
import { AngularFireDatabase, FirebaseListObservable } from 'angularfire2/database';

import { UsersProvider } from '../../providers/users/users';
import { DateProvider } from '../../providers/date/date';

import { DatabaseQuery } from 'angularfire2/interfaces';

@Component({
  selector: 'page-make-room',
  templateUrl: 'makeRoom.html',
})

export class MakeRoomPage {
  selectDeparture: Object = {key:"", value:""};
  departure: string = "";

  selectDestination: Object = {key:"", value:""};
  destination: string = "";

  maxPeople: number = 4; 
  msg: string="";

  nowDate: string = this.dateServices.nowDate;
  nowTime: string = this.dateServices.nowTime;
  today: string = this.dateServices.today; // 
  bookingDate: string = this.nowDate;
  bookingTime: string = this.nowTime;

  min: string = this.dateServices.min;
  max: string = this.dateServices.max;

  user_id: string; 

  spotList: Array<string> = ["", "", "", "", "", "", "", ""];;
  
  constructor(public alertCtrl: AlertController, public navParams: NavParams, public dateServices: DateProvider,
               public navCtrl:NavController, public af: AngularFireDatabase, public userServices: UsersProvider){
    this.user_id = this.userServices.getEmail();
    console.log('constructor makeRoom');
  }

  makeRoom(){
    this.departure = this.selectDeparture['key'] != '' ? this.selectDeparture['key'] : this.selectDeparture['value'];
    this.destination = this.selectDestination['key'] != '' ? this.selectDestination['key'] : this.selectDestination['value'];
    
    // 
    this.msg = "<br>" + " : " + this.departure + "<br>" + 
              " : " + this.destination + "<br>" + 
              " : " + this.bookingDate + "(" + this.dateServices.getKToday(this.bookingDate) + ")" + "<br>" + 
              " : " + this.bookingTime + "<br>" +
              " : " + this.maxPeople + "" + "<br>" ;
    
    //       
    if((this.nowDate+this.nowTime)>(this.bookingDate+this.bookingTime)){
      let alert = this.alertCtrl.create({
        message: "     .",
        buttons: [{
          text: ''
        }]
      });
      alert.present(); 
    } else{
      //    
      if(this.departure == this.destination){
        let alert = this.alertCtrl.create({
          message: "    .",
          buttons: [{
            text: ''
          }]
        });
        alert.present();     
      } else{
        let alert = this.alertCtrl.create({
          title: '',
          subTitle: ' ?',  
          message: this.msg,
          cssClass: 'custom-alrt',
          buttons: [
            { text: '',
              role: 'cancel',
              handler: () => {
                console.log('Cancel clicked');
              }
            },
            { text: '',
              handler: data => {
                console.log(' ');
                console.log(data);
                let roomObj: Object = { departure: this.departure,
                                        destination: this.destination,
                                        departure_date: this.bookingDate,
                                        departure_time: this.bookingTime,
                                        capacity: this.maxPeople,
                                        currentPeople: 1,
                                        host: this.user_id,
                                        participants: [this.user_id]
                                      };
                let chatRoomUrl = this.af.list('/chatrooms/'+this.bookingDate).push(roomObj);
                this.af.list('/rideHistory/'+this.userServices.getUID()+'/'+chatRoomUrl.key).push(roomObj);
                console.log(chatRoomUrl.key);
                console.log('Okay');
                this.navCtrl.setRoot(ChatRoomPage, {chat_room_id:chatRoomUrl.key, bookingDate:this.bookingDate, user_id: this.user_id, whichPage: "makeRoom", roomObj: roomObj});
              }
            }]
        });
        alert.present();
      }
    }
  }
  setDepartureDefault(){
    this.selectDeparture = {key:"", value:""};
  }
  setDestinationDefault(){
    this.selectDestination = {key:"", value:""};
  }
  swapPlace(){
    [this.selectDeparture, this.selectDestination] = [this.selectDestination, this.selectDeparture];
  }
}



